.homePage {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 80vh;
}

.home {
  color: white;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:flex-start;
  height: 80%;
  padding: 30px;
}

.image {
  width: 250px;
  border-radius: 100%;
  border: 1px solid rgb(31, 29, 29);
  box-shadow: 3px 1px 8px rgb(26, 25, 25);
}

.title{
  color: white;
  
  text-shadow: #050505 2px 3px 2px;
}

.line1 {
  font-size: 30px;
  font-weight: 800;
  text-shadow: rgb(31, 29, 29) 0.1em 0.1em 0.3em;
}

.line2 {
  font-size: 22px;
  text-shadow: #050505 1px 1px 1px;
}

.description {
  text-align:right;
}