.projects {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
  padding: 30px;
  font-size: 20px;
  text-shadow: #050505 1px 1px 1px;
}

.modules {
  width: 90vh;
  display: flex;
  justify-content: space-around;
  gap: 50px;
  background-color: #444B47;
  border-radius: 8px;
}

.list {
  display: flex;
  flex-direction: column;
}

.knowledge {
  color: rgb(13, 13, 13);
  text-shadow: rgb(172, 171, 171) 0.1em 0.1em 0.3em;
}